// app/actions.ts
"use server";

import { PrismaClient } from '@prisma/client';
import { revalidatePath } from 'next/cache';

// Evita instanciar múltiples clientes en desarrollo
const globalForPrisma = global as unknown as { prisma: PrismaClient };
const prisma = globalForPrisma.prisma || new PrismaClient();
if (process.env.NODE_ENV !== 'production') globalForPrisma.prisma = prisma;

// --- LEER TODOS LOS DATOS ---
export async function getFinanceData() {
  
  const accounts = await prisma.account.findMany();
  const categories = await prisma.category.findMany();
  const transactions = await prisma.transaction.findMany({ orderBy: { date: 'desc' } });
  const fixedRules = await prisma.fixedRule.findMany();
  const simulatorItems = await prisma.simulatorItem.findMany();

  // Convertimos datos complejos para que React los entienda (Fechas y JSON)
  const parsedCategories = categories.map(c => ({
    ...c,
    subCategories: c.subCategories ? JSON.parse(c.subCategories) : []
  }));

  const parsedTransactions = transactions.map(t => ({
    ...t,
    date: t.date.toISOString(), // React prefiere strings para fechas
    accountId: t.accountId,
    categoryId: t.categoryId
  }));

  return {
    accounts,
    categories: categories.map(c => ({...c, subCategories: c.subCategories ? JSON.parse(c.subCategories) : []})),
    transactions: transactions.map(t => ({...t, date: t.date.toISOString()})),
    fixedRules,
    simulatorItems
  };
}

// --- CREAR TRANSACCIÓN INTELIGENTE ---
export async function createTransaction(data: any) {
  const date = new Date(data.date);

  // CASO 1: TRANSFERENCIA ENTRE CUENTAS
  if (data.type === 'transfer') {
      // 1. Salida (Gasto) de Cuenta A
      await prisma.transaction.create({
          data: {
              type: 'expense',
              amount: data.amount,
              accountId: data.accountId, // Origen
              description: `Transferencia a: ${data.destAccountName}`,
              date: date,
              isTransfer: true,
              relatedAccountId: data.relatedAccountId, // Destino
              didWithdraw: true
          }
      });
      // Restar de Origen
      await prisma.account.update({
          where: { id: data.accountId },
          data: { balance: { decrement: data.amount } }
      });

      // 2. Entrada (Ingreso) a Cuenta B
      await prisma.transaction.create({
          data: {
              type: 'income',
              amount: data.amount,
              accountId: data.relatedAccountId, // Destino
              description: `Recibido de: ${data.originAccountName}`,
              date: date,
              isTransfer: true,
              relatedAccountId: data.accountId, // Origen
              didWithdraw: false // Irrelevante en ingreso, pero por consistencia
          }
      });
      // Sumar a Destino
      await prisma.account.update({
          where: { id: data.relatedAccountId },
          data: { balance: { increment: data.amount } }
      });
  } 
  // CASO 2: MOVIMIENTO NORMAL (Ingreso / Gasto / Ahorro)
  else {
      // Si es "Ahorro que NO se retira", no descontamos saldo
      const shouldUpdateBalance = data.type === 'expense' ? data.didWithdraw : true;

      await prisma.transaction.create({
          data: {
              type: data.type,
              amount: data.amount,
              budgetedAmount: data.budgetedAmount,
              accountId: data.accountId,
              categoryId: data.categoryId,
              description: data.description,
              date: date,
              isFixed: data.isFixed || false,
              isSavings: data.isSavings || false,
              didWithdraw: data.didWithdraw ?? true
          }
      });

      if (shouldUpdateBalance) {
          const multiplier = data.type === 'income' ? 1 : -1;
          await prisma.account.update({
              where: { id: data.accountId },
              data: { balance: { increment: data.amount * multiplier } }
          });
      }

      // CASO ESPECIAL: Ahorro (se retire o no) -> Sumar a la "Cartera de Ahorro" virtual
      // Buscamos o creamos una cuenta virtual de ahorro para llevar el registro
      if (data.isSavings) {
          // Buscamos una cuenta tipo 'savings_virtual'
          let savingsAcc = await prisma.account.findFirst({ where: { type: 'savings_virtual' } });
          
          if (!savingsAcc) {
             savingsAcc = await prisma.account.create({
                 data: { name: 'Bolsillo Ahorros Global', bank: 'Sistema', type: 'savings_virtual', owner: 'Todos', color: '#3b82f6' }
             });
          }

          // Sumamos el valor a esa cuenta virtual (siempre suma, nunca resta)
          await prisma.account.update({
              where: { id: savingsAcc.id },
              data: { balance: { increment: data.amount } }
          });
      }
  }

  revalidatePath('/');
}

// --- ACCIONES DE CUENTAS ---
export async function createAccount(data: any) {
  await prisma.account.create({ data });
  revalidatePath('/');
}

export async function deleteAccount(id: string) {
  await prisma.account.delete({ where: { id } });
  revalidatePath('/');
}

// --- ACCIONES DE CATEGORIAS ---
export async function createCategory(data: any) {
  await prisma.category.create({
    data: {
        name: data.name,
        type: data.type,
        subCategories: JSON.stringify(data.subCategories || [])
    }
  });
  revalidatePath('/');
}
export async function deleteCategory(id: string) {
    await prisma.category.delete({ where: { id } });
    revalidatePath('/');
}

export async function updateCategory(data: any) {
    // Si vienen subcategorías, asegúrate de stringificarlas si es necesario, 
    // pero como Prisma espera string, asumiremos que ya lo manejas o lo convertimos aquí.
    await prisma.category.update({
        where: { id: data.id },
        data: {
            name: data.name,
            type: data.type,
            subCategories: JSON.stringify(data.subCategories) // Guardamos el array como texto
        }
    });
    revalidatePath('/');
}

// --- ACCIONES DE SIMULADOR ---
export async function saveSimulatorItem(data: any) {
    // Si existe ID lo actualiza, si no, crea uno nuevo
    const existing = await prisma.simulatorItem.findUnique({ where: { id: data.id } });
    if (existing) {
        await prisma.simulatorItem.update({ where: { id: data.id }, data: { name: data.name, amount: data.amount } });
    } else {
        await prisma.simulatorItem.create({ data });
    }
    revalidatePath('/simulator');
}

export async function deleteSimulatorItem(id: string) {
    await prisma.simulatorItem.delete({ where: { id } });
    revalidatePath('/simulator');
}

// --- REGLAS FIJAS ---
export async function createFixedRule(data: any) {
    await prisma.fixedRule.create({ data });
    revalidatePath('/');
}

// --- FUNCIÓN PARA POBLAR DATOS INICIALES (SEED) ---
export async function seedDatabase() {
  // 1. Crear Cuentas por defecto
  const acc1 = await prisma.account.create({
    data: { name: 'Davivienda', bank: 'Davivienda', type: 'bank', owner: 'Jhonattan', color: '#ef4444', balance: 0 }
  });
  
  const acc2 = await prisma.account.create({
    data: { name: 'Efectivo', bank: 'Bolsillo', type: 'cash', owner: 'Jhonattan', color: '#22c55e', balance: 0 }
  });

  // 2. Crear Categorías por defecto
  await prisma.category.create({
    data: { name: 'Efectivo', type: 'income', subCategories: JSON.stringify(['Retiro']) }
  });

  
  
  // 2. Categorías Básicas
  const catCount = await prisma.category.count();
  if (catCount === 0) {
      await prisma.category.createMany({
          data: [
            { name: 'Efectivo', type: 'income', subCategories: JSON.stringify(['Retiro'])  }
          ]
      });
  }
  
  // 3. Categoría Especial de Ahorro (Esta era la que fallaba)
  // Usamos upsert: Si existe no hace nada (update vacío), si no existe, la crea.
  await prisma.category.upsert({
    where: { id: 'cat_sys_savings' },
    update: {},
    create: { id: 'cat_sys_savings', name: 'Ahorro Automático', type: 'income', subCategories: '[]' }
  });

  revalidatePath('/');
  return { success: true };
}

// --- ACCIONES FALTANTES QUE NECESITAMOS IMPLEMENTAR ---

export async function updateAccount(data: any) {
  await prisma.account.update({
    where: { id: data.id },
    data: {
        name: data.name,
        bank: data.bank,
        type: data.type,
        color: data.color
    }
  });
  revalidatePath('/');
}

export async function deleteTransaction(id: string) {
    // Primero obtenemos la transacción para saber cuánto devolver a la cuenta
    const tx = await prisma.transaction.findUnique({ where: { id } });
    if (!tx) return;

    await prisma.transaction.delete({ where: { id } });

    // Revertimos el saldo
    // Si era Ingreso (sumó), ahora restamos. Si era Gasto (restó), ahora sumamos.
    // OJO: Si era transferencia o ahorro virtual la lógica se complica, 
    // pero para gastos/ingresos simples esto funciona:
    if (tx.accountId) {
        const multiplier = tx.type === 'income' ? -1 : 1; 
        await prisma.account.update({
            where: { id: tx.accountId },
            data: { balance: { increment: tx.amount * multiplier } }
        });
    }
    revalidatePath('/');
}

export async function updateFixedRule(data: any) {
    await prisma.fixedRule.update({
        where: { id: data.id },
        data: {
            description: data.description,
            budgetedAmount: data.budgetedAmount,
            dayOfMonth: data.dayOfMonth,
            isActive: data.isActive
        }
    });
    revalidatePath('/');
}

export async function deleteFixedRule(id: string) {
    await prisma.fixedRule.delete({ where: { id } });
    revalidatePath('/');
}

